<template>
<!--  主界面区域  -->
  <el-container class="home-container">
    <el-header class="el-header" height="70px">
      <div>
        <img src="../assets/2.png" alt="" width="60" height="70">
        <h1>App信息管理平台</h1>
      </div>
      <el-button type="info" @click="logout">退出</el-button>
    </el-header>
    <el-container>
      <el-aside :width="isCollapse ? '64px' : '200px'" >
        <!--侧边栏菜单区域-->
        <el-menu background-color="#475669" text-color="#fff" active-text-color="#ffd04b"
                 :collapse="isCollapse" :collapse-transition="true" router default-active="$route.path">
          <!--动态绑定实现点击菜单栏后有高亮，$route.path-->
          <!--一级菜单-->
          <el-submenu :index="item.id + ''" v-for="item in menuList" :key="item.id">
            <!--一级菜单模板区-->
            <template slot="title">
              <!--图标-->
              <i :class="iconLists[item.id]"></i>
              <span>{{item.authName}}</span>
            </template>
            <!--二级菜单-->
            <el-menu-item :index="'/' + subItem.path" v-for="subItem in item.children" :key="subItem.id"
            @click="saveNavState">
              <i class="el-icon-menu"></i>
              <span>{{subItem.authName}}</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-container>
        <el-main class="el-main">
          <!--路由占位符-->
          <router-view></router-view>
        </el-main>
        <el-footer class="el-footer" align="center">
          <el-row :gutter="5" style="margin-top: 12px; margin-left: 12px" type="flex" justify="center">
            <el-col :span="6">
              <el-badge :value="1" class="item">
                <el-button size="small">待处理</el-button>
              </el-badge>
            </el-col>
          </el-row>
        </el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data () {
    return {
      menuList: [],
      isCollapse: false,
      iconLists: {
        '100': 'el-icon-user-solid',
        '200': 'el-icon-s-tools',
        '300': 'el-icon-s-check',
        '400': 'el-icon-user-solid',
        '500': 'el-icon-s-check'
      },
      user: {
        username: 'admin',
        password: '123456',
        loginRole: '1'
      }
    }
  },
  created () {
    this.getMenuList()
  },
  mounted () {
  },
  methods: {
    logout: function () {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    async getMenuList () {
      if (this.$store.getters.getMenu === []) {
        return this.$message.error('获取菜单数据失败')
      }
      this.menuList = this.$store.getters.getMenu
      console.log(this.menuList)
    },
    // 点击按钮，切换菜单的折叠与展开
    toggleButtonCollapse () {
      console.log(this.isCollaspe)
      this.isCollaspe = !this.isCollaspe
    },
    saveNavState () {

    }
  }
}
</script>

<style lang="less" scoped>
  .home-container{
    height: 100%;
  }
  .el-header{
    background-color: #2c3e50;
    display: flex;
    justify-content: space-between;
    padding-left: 0;
    align-items: center;
    color: #ffffff;
    font-size: 20px;
    font-family: 华文新魏, monospace;
    > div {
      display: flex;
      align-items: center;
      span {
        margin-left: 15px;
      }
    }
  }
  .el-aside{
    background-color: #475669;
  }
  el.main{
    background-color: #ffffff;
  }
  .el-footer{
    background-color: #f4f4f5;
  }
  .el-menu{
    border-right: none;
  }
  .toggleButton{
    background-color: #0086ac;
    font-size: 12px;
    line-height: 24px;
    color: #fff;
    text-align: center;
    letter-spacing: 0.2em;
    cursor: pointer;
  }
</style>
