<template>
  <div class="register_container">
    <img src="../assets/bg.jpg" alt="" class="bgp"/>
    <div class="login_box">
      <!--头像区域-->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt=""/>
      </div>
      <el-card class="register">
        <el-steps :space="200" :active="activeIndex - 1" finish-status="success" align-center style="margin-top:40px;">
          <el-step title="选择角色"></el-step>
          <el-step title="填写基本信息"></el-step>
          <el-step title="完成"></el-step>
        </el-steps>
        <div style="width: 100%; margin-top: 10px; margin-left: -20px">
          <el-tabs :tab-position="'left'" v-model="activeIndex" :before-leave="beforeTabLeave" style="margin-left: 0">
            <el-tab-pane label="选择角色" name="1" style="height: 100%;">
              <el-select v-model="role" placeholder="请选择" @change="roleSelection">
                <el-option
                  v-for="item in roles"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-tab-pane>
            <el-tab-pane label="基本信息" name="2" style="height: 800px;">
              <el-form v-if="isDeveloper" :model="developer" :rules="addFormRules" ref="addFormRef" class="form">
                <el-form-item label="用户名" size="medium" prop="name">
                  <el-input placeholder="请输入内容" v-model="developer.basicInformation" class="desc">
                  </el-input>
                </el-form-item>
                <el-form-item label="昵称" size="medium" prop="name">
                  <el-input placeholder="请输入内容" v-model="developer.basicInformation" class="desc">
                  </el-input>
                </el-form-item>
                <el-form-item label="密码" size="medium" prop="password">
                  <el-input placeholder="请输入内容" v-model="developer.basicInformation" class="desc">
                  </el-input>
                </el-form-item>
                <el-form-item label="开发者信息" prop="devInformation" size="medium">
                  <el-input type="textarea" placeholder="请输入内容" v-model="developer.basicInformation" class="desc">
                  </el-input>
                </el-form-item>
              </el-form>
              <el-form v-if="isAdministrator" :model="administrator" :rules="addFormRules" ref="addFormRef" class="form">
                <el-form-item label="用户名" size="medium" prop="name">
                  <el-input placeholder="请输入内容" v-model="administrator.basicInformation" class="desc">
                  </el-input>
                </el-form-item>
                <el-form-item label="密码" size="medium" prop="password">
                  <el-input placeholder="请输入内容" v-model="administrator.basicInformation" class="desc">
                  </el-input>
                </el-form-item>
                <el-form-item label="审核权限" size="medium">
                  <el-select v-model="administrator.verifyRange" multiple placeholder="请选择">
                    <el-option
                      v-for="item in censorTypes"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
              </el-form>
            </el-tab-pane>
            <el-tab-pane label="确认页面" name="3">
              <el-form-item style="align-content: center" v-if="isAdministrator">
                <span>
                  <p><i class="el-icon-user-solid"></i>  用户名: {{administrator.username}}</p>
                </span>
                <span>
                  <i class="el-icon-info"></i>  审核权限:
                  <ul v-for="(item, index) in administrator.verifyRange" :key="index">
                    <li>{{item}}</li>
                  </ul>
                </span>
                <el-button type="info" @click="clearAddForm" class="btn" size="small">重置</el-button>
                <el-button type="primary" @click="submitAddForm" class="btn" size="small">确认</el-button>
              </el-form-item>
              <el-form-item style="align-content: center" v-if="isDeveloper">
                <span>
                  <p><i class="el-icon-user-solid"></i>  用户名: {{developer.devUsername}}</p>
                </span>
                <span>
                  <p><i class="el-icon-s-opportunity"></i>  昵称: {{developer.devNickname}}</p>
                </span>
                <span>
                  <p><i class="el-icon-s-operation"></i>  用户信息说明: {{developer.devInformation}}</p>
                </span>
                <el-button type="info" @click="clearAddForm" class="btn" size="small">重置</el-button>
                <el-button type="primary" @click="submitAddForm" class="btn" size="small">确认</el-button>
              </el-form-item>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      activeIndex: '1',
      addFormRules: {
        name: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 30, message: '请输入三至三十个字符', trigger: 'blur' }],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 8, message: '请至少输入八个字符', trigger: 'blur' }],
        devInformation: [
          { required: true, message: '请输入应用版本信息说明', trigger: 'blur' },
          { min: 4, message: '请至少输入四个字符', trigger: 'blur' }]
      },
      roles: [
        {
          value: '管理员',
          label: '管理员'
        },
        {
          value: '开发者',
          label: '开发者'
        }
      ],
      role: '',
      isAdministrator: false,
      isDeveloper: false,
      administrator: {
        username: '',
        password: '',
        verifyRange: []
      },
      developer: {
        username: '',
        password: '',
        nickname: '',
        devInfo: ''
      },
      // {商城,游戏,影音,社交,资讯,学习,工具};App的类型，用于和管理员建立对应关系
      censorTypes: [{
        value: '选项1',
        label: '商城'
      }, {
        value: '选项2',
        label: '游戏'
      }, {
        value: '选项3',
        label: '影音'
      }, {
        value: '选项4',
        label: '社交'
      }, {
        value: '选项5',
        label: '资讯'
      }, {
        value: '选项6',
        label: '学习'
      }, {
        value: '选项7',
        label: '工具'
      }]
    }
  },
  methods: {
    roleSelection () {
      if (this.role === '管理员') {
        this.isAdministrator = true
        this.isDeveloper = false
        this.activeIndex = '2'
      } else {
        this.isAdministrator = false
        this.isDeveloper = true
        this.activeIndex = '2'
      }
    },
    beforeTabLeave (activeName, oldActiveName) {
      console.log('即将离开的标签页名字是:' + oldActiveName)
      console.log('即将进入的标签页名字是:' + activeName)
      if (activeName < oldActiveName) {
        return true
      }
      if (oldActiveName === '1' && this.role === '') {
        this.$message.error('请先选择角色')
        return false
      }
      if (oldActiveName === '2' && (this.administrator === {} || this.developer === {})) {
        this.$message.error('请先完成当前页的信息输入')
        return false
      }
      this.$message.success('系统已为您保存信息')
    },
    clearAddForm () {
      this.administrator = {}
      this.developer = {}
      this.role = ''
      this.activeIndex = '1'
      this.$refs.addFormRef.resetField()
    },
    submitAddForm () {
      this.$refs.addFormRef.validate((valid) => {
        if (valid) {
          console.log(this.addForm)
          this.$message.success('创建应用成功')
          this.$http.push('/goods')
        } else {
          this.$message.error('创建应用失败')
        }
      })
    }
  },
  watch: {
    'addForm.author' () {
      this.$refs.authorRef.$emit('el.form.change')
    }
  }
}
</script>

<style lang="less" scoped>
  .form {
    height: 100%;
  }
  .register_container {
    height: 100%;
  }
  .bgp {
    width: 100%;
    height: 100%;
  }
  .register {
    height: 100%;
  }
  .login_box {
    width: 600px;
    height: 360px;
    background-color: white;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    .avatar_box {
      height: 130px;
      width: 130px;
      border: 1px solid white;
      border-radius: 50%;
      padding: 10px;
      box-shadow: 0 0 10px #d3d4d6;
      position: absolute;
      left: 50%;
      transform: translate(-50%, -60%);
      background-color: #ffffff;
      img{
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: #e9e9eb;
      }
    }
  }
  .login_form {
    position: absolute;
    bottom: 0;
    width:100%;
    padding: 0 20px;
    box-sizing: border-box;
  }

  .el-tabs__header .el-tabs__nav .el-tabs__nav-scroll {
    margin-left: -10px;
  }
</style>
